body {
	margin: 5px;
	font-family: "Tw Cen MT";
}

/*Pokemon Type*/
.rock {
	background-image: linear-gradient(
		to top, #d9856d 0%, #7b6859 100%);
}

.ghost {
	background-image: linear-gradient(
		to top, #a8a7a7 0%, #000000 100%);
}

.electric {
	background-image: linear-gradient(
		to top, #fed400 0%, #c4a300 100%);
}

.bug {
	background-image: linear-gradient(
		to top, #5cbd41 0%, #01ffc0 100%);
}

.poison {
	background-image: linear-gradient(
		to top, #bf4ce6 0%, #4570ad 100%);
}

.normal {
	background-image: linear-gradient(
		to top, #ffffff 0%, #bdbdbd 100%);
}

.fairy {
	background-image: linear-gradient(
		to top,	#fb7d82 0%,	#ff9ee0 100%);
}

.fire {
	background-image: linear-gradient(
		to top, #ffff00 0%, #ff0000 100%);
}

.grass {
	background-image: linear-gradient(
		to top, #08ff21 0%, #3da63b 100%);
}

.water {
	background-image: linear-gradient(
		to top, #89f7fe 0%, #66a6ff 100%);
}

.ground {
	background-image: linear-gradient(
		to top, #772f1a 0%, #f2a65a 100%);
}

.psychic {
  	background-image: linear-gradient(
    	to top, #cc5ae9 0%, #e4abed 100%);
}

.fighting {
	background-image: linear-gradient(
		to top, #969191 0%, #d7c3c3 100%);
}
/*Pokemon Type*/

/*Description Text*/
.descript {
	text-transform: capitalize;
}
.app {
	min-height: 100px;
	padding: 3px 100px;
}

.pokemon {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
}

.all {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
}

h3 {
	text-decoration: underline;
	text-transform: uppercase;
}

.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 10px 2px;
	margin: 3px;
	border: 1px solid #efefef;
	border-radius: 20px;
	min-width: 160px;
	text-align: center;
}

/*Pokemon Number*/
.container .number {
	border-radius: 10px;
	padding: 5px 15px;
	background-color: rgba(255, 255, 255, 0.63);
}

/*Pokemon Image*/
.container img {
	width: 100px;
	height: 100px;
}

.detail {
	display: flex;
	flex-direction: column;
	width: 100%;
}

.detail types{
	text-transform: capitalize;
}

.detail button {
	color: rgb(22, 22, 22);
	padding: 5px;
	margin-top: 3px;
	border-radius: 50px;
	cursor: pointer;
}
/*Capture Button*/
.container capture {
	border: 1px #000000;
	border-radius: 20px;
}

.pokecap {
	margin: 5px 25px;
	padding: 8px;
	border: 1px #000000;
	border-radius: 50px;
	cursor: pointer;
	background-image: linear-gradient(
		#ff0000 100%, #ff0000 50%);
	color:	#ffffff;
}

.pokecap:hover{
	background-image: linear-gradient(
		#ffffff 50%, #ffffff 50%);
	color:#000000;
}
/*Status Button*/
.pokeinfo {
	margin: 5px 25px;
	border-color: #000000;
	background-image: linear-gradient(
		#ffffff 0%, #acacac 100%);
	color: rgb(0, 0, 0);
}

.pokeinfo:hover {
	background-image: linear-gradient(
		#00d0ff 0%,#0088ff 100%);
	color: rgb(255, 255, 255)
}

/*More Pokemon Button*/
.load-more {
	background-image: linear-gradient(
		to bottom, #323232 0%, #000000 100%);
	border-radius: 6px;
	border: 3px solid #828282;
	color: rgb(255, 255, 255);
	padding: 10px 15px;
	min-width: 20%;
	margin-top: 5px;
}

.load-more:hover {
	background-image: linear-gradient(
		#00fffb 0%, #00c8ff9c 100%);
	color: rgb(0, 0, 0);
}